* {
    font-family: monospace;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    line-height: 1;
}

body {
    width: 100vw;
    min-width: 400px;
}

body * {
    display: block;
}

div#App {
    width: 96%;
    margin: auto;
}

section#actions * {
    width: 100%;
    padding: 5px 0;
    margin: 5px 0;
}

#footer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: absolute;
    bottom: 0;
}

.stamps {
    margin: 1vh 0;
}
.stamp {
    width: 100%;
    cursor: pointer;
    min-height: 25vh;
    position: relative;
    margin: 5vh 0;
}

.stamp div {
    margin: 1vh 0;
}

.stamp .stamp-actions {
    display: none;
    position: absolute;
    bottom: 0;
}

.stamp:hover {
    outline: thin solid darkred;
}

.stamp:hover .stamp-actions {
    display: flex;
    gap: 20px;
}

.stamp-actions div:hover {
    cursor: pointer;
    outline: thin solid red;
}
